@import 'fonts/montserrat/montserrat.css';
@import 'variables.scss';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: #{$font-size-base + 'px'};
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1rem;
  line-height: 1.7;
  width: 100%;
  overflow-x: hidden;
}

h1,
.h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: calc_rem(24);
  line-height: 1.25;
  margin: 0;
}

h2,
.h2 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: calc_rem(19);
  line-height: 1.25;
}

h3,
.h3 {
  font-weight: 700;
  font-size: calc_rem(12);
  text-transform: uppercase;
  line-height: 1.25;
}
