@import '@/theme/variables.scss';

.footer {
  background: $white;

  &_bordered_top {
    border-top: 1px solid $dark25;
  }
  
  & &__container,
  & &__bottom-content-wrapper {
    max-width: $max_width_container;
    margin-left: auto;
    margin-right: auto;
    padding-left: 10px;
    padding-right: 10px;
  }
  
  & &__top-content {
    display: grid;
    grid-template-areas: 'col1 col2 col3 col4 col5';
    grid-template-columns: 3fr 2fr 2fr 2fr 3fr;
    padding: 100px 0;
    margin: 0 -20px;

    @media (max-width: $md+1) {
      grid-template-areas: 'col1';
      grid-template-columns: 1fr;
      padding-top: 110px;
      padding-bottom: 0;
    }
  }
  
  & &__top-content-col{
    padding: 0 20px;

    &_first {
      grid-area: col1;

      @media (max-width: $md+1) {
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  
    &_second {
      grid-area: col2;
    }
  
    &_third {
      grid-area: col3;
    }
  
    &_fourth {
      grid-area: col4;
    }
  
    &_fifth {
      grid-area: col5;
    }

    @media (max-width: $md+1) {
      &_second,
      &_third,
      &_fourth,
      &_fifth {
        display: none;
      }
    }
  }

  & &__logo-wrapper {
    display: flex;
    margin-bottom: 20px;

    @media (max-width: $md+1){
      justify-content: center;
      margin-bottom: 18px;
    }
  }

  & &__title-wrapper {
    margin-top: 4px;
    margin-bottom: 23px;
  }

  & &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &-link {
      display: inline-block;
      text-decoration: none;
      color: $dark50;
      margin-bottom: 21px;
      line-height: 1.21;
  
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  & &__bottom-content {
    border-top: 1px solid $dark25;
    padding-top: 23px;
    padding-bottom: 23px;

    @media (max-width: $md+1) {
      border: 0;
      padding-top: 0;
      padding-bottom: 110px;
    }
  }

  & &__bottom-content-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: $md+1) {
      justify-content: center;
    }
  }

  & &__text,
  & &__text-subscription,
  & &__text-copyright {
    color: $dark75;
  }

  & &__text,
  & &__text-subscription {
    margin-bottom: 20px;
  }

  & &__text {
    @media (max-width: $md+1) {
      text-align: center;
      margin-bottom: 28px;
    }
  }

  & &__text-copyright {
    @media (max-width: $md+1) {
      display: none;
    }
  }
}
